    border-radius: 30px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 48%;
    @media (max-width: 768px) {
      width: 100%;
    }
  }

  &__quantity-button {
    font-size: 20px;
    line-height: 0;
    border: none;
    background: #f9f9f9;
    color: #3d3d3d;
    height: 38px;
    width: 30px;
    cursor: pointer;
    &_minus {
      transform: rotate(180deg);
    }
  }
}
</style>
